.step {
  background-color: #ffffff;
  padding: 25px 26px 20px 25px;
  margin-bottom: 10px;
  position: relative;
  border-radius: 10px;

  &:last-child {
    margin-bottom: 0;
  }
}

.step__nav {
  display: none;
}

.step__title {
  @include set-font(20px, 20px, 700);
  letter-spacing: -0.2px;
  color: #1d2e5b;
  padding: 0;
  margin: 0;
  margin-bottom: 15px;
}

.step .step__country-select {
  width: 100%;
}

.step__country-label {
  @include set-font(18px, 18px, 500);
  color: #1d2e5b;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  position: relative;

  &::before {
    position: absolute;
    content: "";
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: #1d2e5b;
    left: 0;
    bottom: 50%;
    transform: translateY(50%) translateX(-50%);
  }

  &::after {
    position: absolute;
    content: "";
    height: 2px;
    width: 14px;
    left: -14px;
    bottom: 50%;
    transform: translateY(50%);
    background-color: #1d2e5b;
  }
}

.step__country-title {
  padding-left: 11px;
  width: 155px;
  text-overflow: clip;
  overflow: hidden;
  white-space: nowrap;
  position: relative;
  margin-right: auto;

  &::after {
    content: "";
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    width: 15px;
    background: linear-gradient(to right, rgba(#ffffff, 0.4), #ffffff 100%);
  }
}

.step__btn {
  @include set-font(17px, 30px, 700);
  width: calc(100% + 10px);
  text-transform: uppercase;
  padding-top: 5px;
  padding-bottom: 5px;
  color: #1d2e5b;
  display: block;
  margin-right: -5px;
  margin-left: -5px;

  & svg {
    display: none;
  }

  &:active {
    color: rgba(#1d2e5b, 0.3);
  }

  &:active svg {
    opacity: 0.3;
  }
}

.step .step__text {
  margin-bottom: 21px;
}

.step .step__checkbox {
  display: block;
  margin-bottom: 20px;
}

.step__textarea-wrapper {
  position: relative;
  width: 100%;

  &--line::before {
    position: absolute;
    content: "";
    width: 2px;
    height: 279px;
    top: -26px;
    left: -14px;
    background-color: #1d2e5b;
  }
}

.step__textarea-invalid {
  @include set-font(16px, 16px, 400);
  color: #ff0000;
  background-color: #ffefef;
  border-radius: 4px;
  width: 100%;
  height: 60px;
  padding: 10px 15px;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 1;
  display: none;
}

.step__textarea {
  @include set-font(16px, 22px, 400);
  color: $grey;
  display: block;
  width: 100%;
  height: 220px;
  resize: none;
  border: 1px solid #cbced9;
  border-radius: 3px;
  margin-bottom: 22px;
  position: relative;
  background-color: transparent;
  z-index: 2;
  padding: 8px 13px;

  &:hover {
    border-color: #959bb2;
  }

  &:focus {
    border-color: $blue-black;
  }

  &:invalid {
    border-color: #f47e7e;
  }

  &:invalid ~ .step__textarea-invalid {
    display: block;
  }

  &::-webkit-input-placeholder {
    color: rgba($grey, 0.3);
  }

  &::-moz-placeholder {
    color: rgba($grey, 0.3);
  }

  &:-ms-input-placeholder {
    color: rgba($grey, 0.3);
  }

  &:-moz-placeholder {
    color: rgba($grey, 0.3);
  }
}

.step__btn--fwd {
  &:hover,
  &:focus {
    background-color: #ffbd30;
  }
}

.step__btn--back {
  padding-top: 0;
  padding-bottom: 0;
  margin-top: 13px;
  margin-bottom: -11px;

  &:hover,
  &:focus {
    background-color: transparent;
    color: rgba(#1d2e5b, 0.7);
  }

  &:hover svg,
  &:focus svg {
    opacity: 0.7;
  }
}

.step__text br {
  display: none;
}

@media only screen and (min-width: $tablet-width) {
  .step__textarea {
    @include set-font(20px, 24px, 400);
    padding: 16px 32px 16px 18px;
    height: 142px;
  }

  .step__title {
    @include set-font(36px, 36px, 700);
    margin-bottom: 36px;
  }

  .step__btn {
    @include set-font(24px, 30px, 700);
    padding-top: 26px;
    padding-bottom: 26px;
    padding-right: 29px;
    padding-left: 31px;
    margin-top: 45px;
    margin-right: 0;
    margin-left: 2px;
    border-radius: 15px;
    text-transform: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
    order: 3;

    &--fwd {
      width: 53%;
    }

    &--back {
      width: auto;
      margin-left: auto;
      padding: 0;
      margin-bottom: 0;
      margin-right: -2px;
    }

    & svg {
      display: block;
      width: 10px;
      height: 14px;
      margin-right: 4px;
      fill: #1d2e5b;
    }

    &--back svg {
      margin-right: 21px;
    }
  }

  .step__country-label {
    @include set-font(30px, 30px, 700);
    width: 100%;
    margin-top: 20px;
    margin-bottom: 30px;
    letter-spacing: -0.1px;

    & img {
      margin-right: 31px;
      width: 70px;
      height: 47px;
    }

    &::after {
      display: none;
    }

    &::before {
      left: auto;
      right: 5px;
      top: 10px;
      transform: translateY(50%) translateX(50%);
      width: 15px;
      height: 15px;
    }
  }

  .step__country-title {
    width: 400px;
    padding-left: 0;
  }

  .step__textarea-wrapper {
    width: calc(100% - 30px);

    &::before {
      width: 3px;
      left: auto;
      right: -26px;
      height: 271px;
      top: -57px;
    }
  }

  .step__textarea-invalid {
    font-size: 20px;
    height: 57px;
    padding: 17px 24px 20px;
    bottom: 20px;
  }

  .step .step__text {
    @include set-font(20px, 24px, 400);
    margin-bottom: 43px;
    position: relative;
    width: 100%;

    &::after {
      position: absolute;
      content: "";
      width: 100%;
      height: 1px;
      background-color: #d6d9e6;
      left: 0;
      bottom: -41px;
    }
  }

  .step .step__calendar {
    width: 100%;
    order: 2;
  }

  .step .step__input-numbers {
    width: 100%;

    &--duration {
      order: 1;
    }
  }

  .step .step__checkbox {
    display: block;
    margin-bottom: 0;
    padding-bottom: 30px;
  }

  .step .step__checkbox-text {
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 2px;
  }

  .step--entertainment {
    & .step__text {
      margin-bottom: 42px;

      &::after {
        bottom: -36px;
      }
    }

    & .step__btn {
      margin-top: 24px;
    }
  }

  .step {
    display: flex;
    flex-wrap: wrap;
    padding: 44px 45px 55px 43px;
    margin-bottom: 40px;
    border-radius: 30px;

    &--countries {
      margin-bottom: 49px;

      & .step__btn {
        margin-top: 32px;
      }

      & .step__text {
        margin-bottom: 77px;
        position: relative;

        &::after {
          bottom: -37px;
        }
      }
    }
  }

  .step__text br {
    display: inline;
  }
}

@media only screen and (min-width: $desktop-width) {
  .step {
    padding-top: 95px;
    padding-right: 161px;
    padding-bottom: 106px;
    padding-left: 160px;
    margin-bottom: 44px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    border-radius: 60px;
  }

  .step__title {
    margin-bottom: 37px;
  }

  .step__btn {
    width: 28%;
    order: 3;
    margin-top: 61px;

    &--back {
      width: auto;
      margin-right: 2px;
    }
  }

  .step .step__calendar {
    width: 100%;
    order: 2;
  }

  .step .step__input-numbers--duration {
    order: 1;
  }

  .step__title,
  .step .step__text {
    width: 70%;
  }

  .step .step__text {
    @include set-font(20px, 24px, 400);
    margin-bottom: 85px;

    &::after {
      width: 1000px;
      bottom: -55px;
    }
  }

  .step__nav {
    display: flex;
    flex-direction: column;
    margin-top: -61px;
    width: 30%;
  }

  .step__nav-link {
    @include set-font(20px, 20px, 400);
    text-transform: uppercase;
    color: #000000;
    opacity: 0.6;
    margin-bottom: 24px;
    padding-left: 115px;
    position: relative;
    letter-spacing: 2.1px;

    &:last-child {
      margin-bottom: 0;
    }

    &[href] {
      opacity: 1;
    }

    &::before {
      position: absolute;
      content: "";
      width: 5px;
      height: 5px;
      bottom: 50%;
      transform: translateY(50%);
      left: 71px;
      background-color: #1d2e5b;
      border-radius: 50%;
    }
  }

  .step__title br {
    display: none;
  }

  .step__textarea-wrapper {
    margin-right: 270px;

    &::before {
      right: -45px;
    }
  }

  .step .step__input-numbers {
    width: 55%;
  }

  .step .step__checkbox {
    padding-bottom: 0;
    align-self: center;
    margin-top: -17px;
  }

  .step__country-label {
    & img {
      margin-right: 115px;
    }

    &::before {
      right: 227px;
    }
  }

  .step__textarea {
    padding: 23px 18px 20px 25px;
  }

  .step__country-title img {
    position: relative;
    top: 5px;
  }

  .step--countries {
    & .step__text {
      margin-bottom: 117px;
    }

    & .step__btn {
      margin-top: 38px;
    }
  }

  .step--entertainment {
    & .step__text {
      margin-bottom: 107px;
    }

    & .step__btn {
      margin-top: 33px;
    }
  }
}
