.companions {
  @include set-bb-radius(30px);
  position: relative;
  margin-top: -30px;
  padding-top: 30px;
  z-index: 17;
  background-color: $blue-white;
  background-image: url("../img/smolov-back-desktop@1x.png"), url("../img/rogovaya-back-mobile@1x.png");
  background-repeat: no-repeat, no-repeat;
  background-size: 143px 143px, 71px 71px;
  background-position: top 69px right 25px, top 342px left -11px;

  &::before {
    position: absolute;
    content: "";
    background-color: #141b3b;
    width: 132px;
    height: 132px;
    border-radius: 15px;
    top: 70px;
    left: -22px;
  }

  @media (min-resolution: 1.5dppx) {
    background-image: url("../img/smolov-back-desktop@2x.png"), url("../img/rogovaya-back-mobile@2x.png");
  }

  @media only screen and (min-width: $tablet-width) {
    background-image: url("../img/smolov-back-desktop@1x.png"), url("../img/rogovaya-back-desktop@1x.png");
    background-size: 143px auto, 285px auto;
    background-position: top 486px left 40px, top 140px right 75px;

    &::before {
      width: 216px;
      height: 213px;
      border-radius: 20px;
      top: 137px;
      left: 34px;
    }

    @media (min-resolution: 1.5dppx) {
      background-image: url("../img/smolov-back-desktop@2x.png"), url("../img/rogovaya-back-desktop@2x.png");
    }
  }

  @media only screen and (min-width: $desktop-width) {
    background-position: top 192px left 547px, top 444px left 191px;

    &::before {
      top: 129px;
      left: 57px;
    }
  }
}

.companions .companions__container {
  position: relative;
  padding-top: 70px;
  padding-bottom: 112px;
}

.companions__list {
  @include ul-style-reset;
  margin-top: 58px;
}

.companions__info {
  padding-left: 25px;
  padding-right: 24px;
}

.companions__show-btn {
  position: absolute;
  bottom: 31px;
  left: 25px;
  width: calc(100% - 25px * 2);
  border-radius: 10px;
  color: #1d2e5b;
  padding-left: 23px;
  padding-right: 23px;
}

.companions .companions__title {
  margin-bottom: 20px;
}

.companions .companions__text {
  margin-bottom: 23px;

  &:last-child {
    margin-bottom: 0;
  }
}

.companions__text br {
  display: none;
}

.companions__list .companions__item {
  margin-bottom: 10px;

  &:last-child {
    margin-bottom: 0;
  }
}

@media only screen and (min-width: $tablet-width) {
  .companions {
    @include set-bb-radius(60px);
    margin-top: -60px;
    padding-top: 60px;
  }

  .companions .companions__container {
    padding-left: 54px;
    padding-right: 54px;
    padding-top: 138px;
    padding-bottom: 249px;
  }

  .companions__show-btn {
    bottom: 89px;
    left: 55px;
    width: calc(100% - 55px * 2);
    border-radius: 15px;
    padding-left: 35px;
    padding-right: 35px;
  }

  .companions__info {
    padding-left: 0;
    padding-right: 180px;
  }

  .companions__list {
    display: flex;
    margin-top: 60px;
  }

  .companions__list .companions__item {
    margin-right: 50px;
    flex-grow: 1;
    min-width: 285px;
    margin-bottom: 0;

    &:nth-child(2n) {
      margin-right: 0;
    }
  }

  .companions .companions__title {
    margin-bottom: 58px;
  }

  .companions .companions__text {
    margin-bottom: 40px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .companions__text br {
    display: inline;
  }
}

@media only screen and (min-width: $desktop-width) {
  .companions .companions__container {
    display: flex;
    padding-left: 101px;
    padding-right: 30px;
    padding-top: 125px;
    padding-bottom: 132px;
  }

  .companions__show-btn {
    position: static;
    display: flex;
    width: 280px;
    margin-top: 86px;
  }

  .companions__info {
    padding-right: 0;
    margin-right: 190px;
  }

  .companions__list {
    margin-top: 8px;
  }

  .companions .companions__title {
    margin-left: -3px;
  }

  .companions__list .companions__item {
    margin-right: 60px;

    &:nth-child(2n) {
      margin-right: 0;
    }
  }
}
