.pagination {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  background-color: #ffffff;
  border-radius: 20px;
}

.pagination__btn {
  border: 0 none;
  padding: 0;
  background-color: transparent;
  height: 50px;
  width: 50%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;

  &:first-child::after {
    content: "";
    position: absolute;
    background-color: #edeff6;
    left: 100%;
    width: 1px;
    top: 0;
    bottom: 0;
  }

  & svg {
    fill: $blue-white;
  }

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.pagination__list {
  @include ul-style-reset;
  display: flex;
}

.pagination__controls {
  display: flex;
  width: 100%;
}

.pagination__link {
  @include set-font(20px, 30px, 700);
  color: $blue-black;
  width: 100%;
  height: 100%;
}

@media only screen and (min-width: $tablet-width) {
  .pagination {
    height: 100px;
  }

  .pagination__controls {
    display: flex;
    width: 20%;
  }

  .pagination__btn {
    height: 100px;

    &:first-child {
      padding-left: 40px;
    }

    &:last-child {
      padding-right: 10px;
    }

    &:first-child::after {
      display: none;
    }
  }

  .pagination__list {
    @include ul-style-reset;
    display: flex;
    margin-left: 28px;
  }

  .pagination__link {
    display: block;
    padding: 10px 20px;
    border-radius: 50%;

    &[href]:hover,
    &[href]:focus {
      color: rgba($blue-black, 0.6);
    }

    &:not([href]),
    &:active {
      background-color: $yellow;
    }
  }

  .pagination__item {
    margin-right: -1px;
  }
}

@media only screen and (min-width: $desktop-width) {
  .pagination__btn {
    height: 100px;

    &:first-child {
      width: 38%;
      padding-left: 31px;
    }

    &:last-child {
      width: 38%;
      padding-right: 24px;
    }
  }
}
