.page-header--js .page-header__nav {
  position: absolute;
  top: 100%;
  width: 100%;
  right: 0;
  left: 0;
  display: none;
}

.page-header__toggle-menu {
  display: none;
}

.page-header__logo-link {
  margin-top: 19px;
  margin-bottom: 25px;
}

.page-header--js .page-header__toggle-menu {
  display: block;
  border: 0 none;
  background-color: transparent;
  cursor: pointer;
  width: 25px;
  height: 20px;
  text-align: center;
  margin-top: -9px;
  margin-right: 5px;
  margin-left: auto;
}

.page-header__menu-open {
  fill: #ffffff;
}

.page-header__menu-close {
  display: none;
  fill: #cccccc;
}

.page-header .page-header__logo--white {
  display: none;
}

.page-header--js .page-header__logo--blue {
  display: none;
}

.page-header--js .page-header__logo--white {
  display: block;
}

.page-header .page-header__container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
}

.page-header--js .page-header__nav-list {
  width: 320px;
  padding-left: 25px;
  padding-right: 25px;
  margin-left: auto;
  margin-right: auto;
}

.page-header .page-header__auth-btn {
  @include set-font(17px, 30px, 700);
  text-transform: uppercase;
  padding-top: 10px;
  padding-bottom: 10px;
  display: block;
  width: auto;
  border-radius: 50px;
}

.page-header__contacts--top {
  display: none;
}

.page-header__contacts--main .page-header__auth {
  width: auto;
  flex-grow: 1;
  margin-bottom: 0;
}

.page-header--main .page-header__logo-link {
  margin-top: 27px;
  margin-bottom: 16px;
}

.page-header--main .page-header__toggle-menu {
  margin-top: 8px;
}

.page-header {
  background-color: #ffffff;
  z-index: 300;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;

  &--js {
    background-color: $blue-black;
  }

  &--opened {
    background-color: #ffffff;

    & .page-header__menu-close {
      display: block;
    }

    & .page-header__menu-open {
      display: none;
    }

    & .page-header__nav {
      display: block;
    }

    & .page-header__logo--white {
      display: none;
    }

    & .page-header__logo--blue {
      display: block;
    }
  }
}

.page-header--scrolled {
  background-color: #ffffff;

  & .page-header__menu-open {
    fill: #cccccc;
  }

  & .page-header__logo--white {
    display: none;
  }

  & .page-header__logo--blue {
    display: block;
  }
}

@media only screen and (min-width: $tablet-width) {
  .page-header--main {
    background-color: #ffffff;

    &.page-header--js {
      background-color: #182044;
    }

    &.page-header--opened {
      background-color: #ffffff;
    }

    &.page-header--scrolled {
      background-color: #ffffff;
    }
  }

  .page-header--js .page-header__nav-list {
    width: $tablet-width;
  }

  .page-header__contacts--top {
    display: block;
  }

  .page-header__contacts--main {
    position: static;
    transform: none;
  }

  .page-header .page-header__nav-list {
    padding-left: 78px;
    padding-right: 45px;
  }

  .page-header .page-header__social {
    padding-left: 95px;
    padding-right: 45px;
  }

  .page-header__logo-link {
    margin-top: 45px;
    margin-bottom: 40px;
  }

  .page-header .page-header__auth-btn {
    @include set-font(20px, 30px, 700);
    padding: 10px 30px;
  }

  .page-header__contacts {
    width: auto;
    position: static;
    display: flex;
    align-items: center;
  }

  .page-header__contacts .page-header__contacts-item {
    display: none;
  }

  .page-header__contacts .page-header__auth {
    display: block;
    margin-bottom: 0;
  }

  .page-header__contacts--main .page-header__auth {
    margin-bottom: -23px;
  }

  .page-header--js .page-header__toggle-menu {
    margin-top: 0;
    margin-right: -11px;
    margin-left: 0;
  }

  .page-header--main .page-header__logo-link {
    margin-top: 45px;
    margin-bottom: 16px;
  }

  .page-header--main .page-header__toggle-menu {
    margin-top: 24px;
    margin-right: -12px;
  }
}

@media only screen and (min-width: $desktop-width) {
  .page-header .page-header__container {
    display: flex;
    justify-content: flex-start;
    padding-top: 48px;
    padding-bottom: 0;
  }

  .page-header .page-header__nav {
    display: flex;
    position: relative;
    width: auto;
    padding-left: 145px;

    &::before {
      content: "";
      position: absolute;
      width: 60px;
      height: 2px;
      bottom: 50%;
      transform: translateY(50%);
      left: 40px;
      background-color: #ffffff;
    }
  }

  .page-header .page-header__site-link {
    color: #ffffff;
    letter-spacing: 0;
  }

  .page-header .page-header__toggle-menu {
    display: none;
  }

  .page-header .page-header__nav-list {
    display: none;
  }

  .page-header .page-header__site {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: auto;
    padding: 0;
    margin: 0;
  }

  .page-header__logo-link {
    margin-top: 0;
    margin-bottom: 0;
  }

  .page-header__contacts {
    order: 1;
    margin-right: -1px;
    margin-top: -3px;
  }

  .page-header__contacts .page-header__contacts-item {
    display: block;
    margin-bottom: 0;
  }

  .page-header .page-header__logo--white {
    display: block;
  }

  .page-header .page-header__logo--blue {
    display: none;
  }

  .page-header--opened {
    & .page-header__logo--white {
      display: block;
    }

    & .page-header__logo--blue {
      display: none;
    }
  }

  .page-header__contacts--main .page-header__auth {
    margin-top: 0;
  }

  .page-header {
    background-color: $blue-black;

    &--scrolled {
      background-color: #ffffff;

      & .page-header__site-link {
        color: $blue-white;
      }

      & .page-header__nav::before {
        background-color: $blue-white;
      }

      & .page-header__logo--blue {
        display: block;
      }

      & .page-header__logo--white {
        display: none;
      }
    }
  }

  .page-header--main {
    background-color: $blue-black;

    &.page-header--js {
      background-color: $blue-black;
    }

    &.page-header--opened {
      background-color: $blue-black;
    }

    &.page-header--scrolled {
      background-color: #ffffff;
    }

    & .page-header__logo-link {
      margin-top: 0;
      margin-bottom: 0;
    }

    & .page-header__site-item {
      margin-right: 36px;
    }

    & .page-header__site-link {
      letter-spacing: -0.2px;
    }
  }

  .page-header--scrolled .page-header__container {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
