.tags {
  @include ul-style-reset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.tags__item {
  @include set-font(16px, 18px, 400);
  color: $grey;
}

.tags--small .tags__item {
  @include set-font(15px, 18px, 400);
}

.tags--medium .tags__item {
  @include set-font(16px, 22px, 400);
}

.tags--textarea {
  @include set-font(16px, 22px, 400);
  width: 100%;
  height: auto;
  background-color: transparent;
  border: 1px solid #cbced9;
  border-radius: 10px;
  resize: vertical;

  &:hover {
    border: 1px solid #aeb4cb;
  }

  &:focus {
    border: 1px solid $blue-black;
  }

  &::-webkit-input-placeholder {
    color: rgba($grey, 0.6);
  }

  &::-moz-placeholder {
    color: rgba($grey, 0.6);
  }

  &:-ms-input-placeholder {
    color: rgba($grey, 0.6);
  }

  &:-moz-placeholder {
    color: rgba($grey, 0.6);
  }
}

@media only screen and (min-width: $tablet-width) {
  .tags__item {
    @include set-font(20px, 20px, 400);
  }

  .tags--textarea {
    @include set-font(20px, 24px, 400);
  }
}
